* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  transition: background-color 1s;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: 40px 20px;
  display: flex;
}

.container button {
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 8px;
  margin: 12px;
  padding: 12px 24px;
  font-size: 24px;
  font-weight: 700;
}

.timer-container {
  flex-direction: column;
}

.timer {
  flex-direction: row;
  font-size: 28px;
  display: flex;
}

.value {
  font-weight: 500;
}

.field:not(:last-child) {
  margin-right: 24px;
}

#datetime-picker {
  border-radius: 8px;
  padding: 12px 8px;
  font-size: 24px;
}

[data-start] {
  cursor: pointer;
  border-radius: 8px;
  margin: 12px;
  font-size: 24px;
  font-weight: 700;
}

.input {
  margin-bottom: 24px;
}
